$break-90: 450px;
$break-80: 400px;
$break-70: 350px;
$break-60: 300px;

html, body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

html {
  body {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    background-color: hsl(222, 11%, 18%);
    color: white;
  }

    #main-logo {
      height: 30%;
      width: 100%;
      /**flexbox**/
      flex: none;
      svg {
        height: 100%;
        width: 100%;
      }
    }
    .center-links {
      width: 80%;
      max-width: 45em; //so it's not too big on a large monitor
      /**spacing**/
      margin-top: 5%;
      margin-bottom: 25%;
      /**flexbox**/
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      /**internal spacing**/
      :not(:last-child) {
        margin-right: 1%;
      }
      a {
        /**internal**/
        text-decoration: none;
        color: inherit;
        font-weight: bold;
        font-size: 100%;
        line-height: 50px;
        text-align: center;
        /**container**/
        height: 50px; //redundant?
        margin: auto;
        box-sizing: border-box;
        border: 2px solid white;
        /**flexbox**/
        flex: 1 1 auto;
        @media only screen and (max-width: $break-90) {
          font-size: 90%;
        }
        @media only screen and (max-width: $break-80) {
          font-size: 80%;
        }
        @media only screen and (max-width: $break-70) {
          font-size: 70%;
        }
        @media only screen and (max-width: $break-60) {
          font-size: 60%;
        }
      }
    }
  }
